import React from 'react';
import { AuthProvider } from '../context/AuthContext'; // Correct path
import Home from './Home'; // Correct path (if Home.js is in the same folder)
import Login from '../components/Login'; // Correct path
import Signup from '../components/Signup'; // Correct path
import StudentDashboard from './StudentDashboard'; // Correct path
import ConsultantDashboard from './ConsultantDashboard'; // Correct path
import Navbar from '../components/Navbar'; // Correct path
import { BrowserRouter as Router, Routes, Route } from 'react-router-dom';

function App() {
  return (
    <AuthProvider>
      <Router>
        <Navbar />
        <Routes>
          <Route path="/" element={<Home />} />
          <Route path="/login" element={<Login />} />
          <Route path="/signup" element={<Signup />} />
          <Route path="/student" element={<StudentDashboard />} />
          <Route path="/consultant" element={<ConsultantDashboard />} />
        </Routes>
      </Router>
    </AuthProvider>
  );
}

export default App;